:root {
  --purple: hsl(259, 100%, 65%);
  --light-red: hsl(0, 100%, 67%);
  --white: hsl(0, 0%, 100%);
  --off-white: hsl(0, 0%, 94%);
  --light-grey: hsl(0, 0%, 86%);
  --smokey-grey: hsl(0, 1%, 44%);
  --off-black: hsl(0, 0%, 8%);
}

:root {
  --font-poppins: "Poppins", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  background-color: var(--off-white);
  font-family: var(--font-poppins);
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h2 {
  font-size: 2.5rem;
}
@media (min-width: 47.9375em) {
  h2 {
    font-size: 3.75rem;
  }
}

p {
  font-size: 2rem;
}

.app {
  background: var(--white);
  border-radius: 5% 5% 25% 5%;
  margin: 5rem auto 0;
  max-width: 40.625rem;
  width: 90vw;
}
@media (min-width: 47.9375em) {
  .app {
    margin-top: 20vh;
  }
}

.container {
  margin: 0 1.5rem;
  padding: 3rem 0 3.75rem;
}
@media (min-width: 47.9375em) {
  .container {
    margin: 0 3rem;
  }
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.form {
  border-bottom: 0.0625rem solid var(--light-grey);
  padding-top: 3rem;
  position: relative;
}
@media (min-width: 47.9375em) {
  .form {
    padding-bottom: 2.5rem;
  }
}
.form button {
  background: var(--purple);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  padding: 1.125rem;
  position: absolute;
  left: calc(50% - 1.984375rem);
  top: calc(100% - 1.984375rem);
  transition: background 0.25s ease-out;
  width: 3.96875rem;
}
.form button:hover, .form button:focus {
  background: var(--off-black);
  outline: none;
}
@media (min-width: 47.9375em) {
  .form button {
    left: calc(100% - 3.9375rem);
    width: 4.46875rem;
  }
}
.form button img {
  height: 1.5rem;
}
@media (min-width: 47.9375em) {
  .form button img {
    height: 2rem;
  }
}

.input {
  width: 33.33%;
  max-width: 7.1875rem;
}
.input:first-child, .input:nth-child(2) {
  margin-right: 1rem;
}
@media (min-width: 47.9375em) {
  .input:first-child, .input:nth-child(2) {
    margin-right: 1.875rem;
  }
}
.input__label {
  color: var(--smokey-grey);
  font-size: 0.75rem;
  font-weight: 700;
  letter-spacing: 0.21875rem;
  margin-bottom: 0.3125rem;
  text-transform: uppercase;
}
.input__input {
  border: 1px solid var(--light-grey);
  border-radius: 8px;
  cursor: pointer;
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 0.3125rem;
  padding: 0.75rem;
  padding-right: 0.125rem;
  width: 100%;
}
.input__input:focus {
  border: 1px solid var(--purple);
  outline: none;
}
.input__required, .input__invalid {
  color: var(--light-red);
  font-size: 0.5rem;
  display: none;
  margin: 0;
}
@media (min-width: 47.9375em) {
  .input__required, .input__invalid {
    font-size: 0.625rem;
  }
}

.output {
  padding-bottom: 3rem;
}
.output__label {
  font-size: 3.25rem;
  font-style: italic;
  font-weight: 800;
  line-height: 1.1;
  margin: 0;
  text-transform: lowercase;
}
@media (min-width: 47.9375em) {
  .output__label {
    font-size: 5.125rem;
  }
}
.output__number {
  color: var(--purple);
  margin-right: -0.3125rem;
}